.header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0px 6px;
    background-color: #fff;
    box-shadow: 0 1px 1px rgba(0, 0, 0, 0.1);
    position: relative;
    z-index: 1000;
  }
  
  .logo img {
    height: 90px;
  }
  
  .desktop-nav {
    display: flex;
    margin-right: 50px;
  }
  
  .desktop-nav a {
    text-decoration: none;
    color: #707070;
    font-weight: 500;
    padding: 5px 23px;
    border-right: 2px solid #D0D0D0;
  }
  
  .desktop-nav .dropdown {
    position: relative;
    margin-top: 5px;
  }
  
  
  .desktop-nav .dropdown-content {
    display: none;
    position: absolute;
    top: 100%;
    right: 0;
    width: 330px;
    background-color: #fff;
    border: none;
    box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
    padding: 10px 0;
    border-radius: 5px;
  }
  
  .desktop-nav .dropdown-content a {
    display: block;
    text-decoration: none;
    color: #000000;
    font-weight: 600;
    padding: 10px 0;
    margin: 0px 20px;
    border: none;
    text-align: left;
  }
  
  
  
  .desktop-nav .dropdown-content a:hover {
    background-color: #f8f9fa;
  }
  
  .desktop-nav .dropdown:hover .dropdown-content {
    display: block;
  }
  
  
  .menu-toggle {
    font-size: 24px;
    background: none;
    margin-right: 35px;
    border: none;
    color: #000;
    cursor: pointer;
    display: none;
  }
  
  .mobile-nav {
    display: none;
    flex-direction: column;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: white;
    transform: translateX(-100%);
    transition: transform 0.3s ease-in-out;
    padding-top: 20px;
    z-index: 1000;
    overflow-y: auto;
  }
  
  .mobile-nav.open {
    transform: translateX(0);
  }
  
  .mobile-nav a {
    text-decoration: none;
    color: #707070;
    font-weight: 500;
    padding: 15px 20px;
    border-bottom: 1px solid #D0D0D0;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  
  .arrow {
    font-size: 18px;
    color: #707070;
    width: 50px;
  }
  
  
  .logsign{
    color: #4384E6;
    text-decoration: underline;
  }
  
  .mobile-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 20px;
    border-bottom: 1px solid #D0D0D0;
    margin-bottom: 10px;
  }
  
  .mobile-logo {
    height: 90px;
  }
  
  .close-menu {
    font-size: 40px;
    background: none;
    margin-right: 20px;
    border: none;
    color: #000;
    cursor: pointer;
  }
  
  .auth-links {
    display: flex;
    border: 1px solid #EBEBEB;
    border-radius: 5px;
    flex-direction: column;
    margin: 20px;
    padding: 0 20px;
  }
  
  .auth-links a {
    text-decoration: none;
    color: #000000;
    font-weight: 600;
    padding: 10px 0;
    text-align: left;
  }
  
  .overlay {
    display: none;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.5);
    opacity: 0;
    transition: opacity 0.3s ease-in-out;
    z-index: 999;
  }
  
  .overlay.show {
    display: block;
    opacity: 1;
  }
  
  @media (max-width: 1368px) {
    .desktop-nav {
      display: none;
    }
  
    .menu-toggle {
      display: block;
    }
  
    .mobile-nav {
      display: flex;
    }
  }
  