.health-card-container {
    padding: 20px;
    background-color: #fff;
    margin-left: 30px;
    position: relative; 
    z-index: 40; 
    margin-top: -120px;
  }
  
  
  
  
  
  .health-card-header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 30px;
  }
  
  .health-card-header-line{
    margin-bottom: 10px;
  }
  
  .health-card-title1 {
    font-size: 16px;
    color: #707070;
    font-weight: 500;
  }
  
  .health-card-subtitle {
    font-size: 28px !important;
    font-weight:normal;
    color: #707070;
    margin-top: 10px;
    flex-grow: 1;
    margin-left: 10px;
  }
  
  .health-card-subtitle span {
    background: linear-gradient(90deg, #F1CA72 0%, #D96E6C 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    font-style: italic;
    font-weight: bold;
  }
  
  .health-card-arrows {
    display: flex;
    align-items: center;
    margin-right: 40px;
  }
  
  .arrow-icon {
    font-size: 24px;
    margin-left: 10px;
    cursor: pointer;
    color: #000;
    transition: color 0.3s ease;
  }
  
  .arrow-icon:hover {
    color: #ff5722; 
  }
  
  .health-card-list {
    display: flex;
    overflow-x: auto;
    scroll-behavior: smooth;
  }
  
  .health-card-list::-webkit-scrollbar {
    display: none;
  }
  
  
  
  .health-card-titles {
    display: flex;
    justify-content: center;
    justify-content:flex-start;
    overflow: auto;
    width: 90%;
    padding: 20px;
  }
  
  .health-card-titles::-webkit-scrollbar {
    display: none;
  }
  
  .health-card-title-button {
    background: none;
    margin: 0 10px;
    padding: 4px 15px;
    border: 1px solid #cfc8c8;
    border-radius: 25px;
    font-size: 15px;
    width: auto;
    font-weight:700;
    cursor: pointer;
    color: #888;
    white-space: nowrap;
  }
  
  .health-card-title-button.active {
    background: #000;
    color: white;
    font-weight: normal;
  
  }
  
  
  .health-card {
    background-color: white;
    border-radius: 16px;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    min-width: 443px;
    height: 485px;
    margin-right: 20px;
    overflow: hidden;
    display: flex;
    flex-direction: column;
    margin-bottom: 30px;
  }
  
  .health-card::-webkit-scrollbar {
    display: none;
  }
  
  
  .health-card-image {
    width: 100%;
    height: 320px;
    object-fit: cover;
  }
  
  .health-card-content {
    padding: 16px;
    display: flex;
    flex-direction: column;
    position: relative;
  }
  
  .health-card-metric {
    background-color: rgba(255, 255, 255, 0.87)  ;
    color: #000;
    position: absolute;
    top: -55px;
    width: 170px;
    height: 42px;
    border-radius: 67px;
    font-size: 16px;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 3px;
    font-weight: medium;
  }
  
  .health-card-metric img{
    width: 24px;
    height: 21px;
    margin-right: 8px;
  }
  
  .health-card-metric span{
    font-size: 14px;
    font-weight: medium;
    color: #919191;
  }
  
  
  
  .health-card-title {
    font-size: 20px;
    font-weight: medium;
    margin-bottom: -12px !important;
    color: #000;
  }
  
  .health-card-description {
    font-size: 17px;
    color: #919191;
    font-weight:normal;
    line-height: 1.5;
  }
  
  .health-card-list {
    display: flex;
    overflow-x: auto;
    scroll-behavior: smooth;
  }
  
  .health-card-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 16px;
  }
  
  .health-card-title {
    font-size: 24px;
    font-weight: bold;
    margin: 0;
  }
  
  .health-card-subtitle {
    font-size: 14px;
    color: #888;
    margin: 0;
  }
  
  .arrow-icon {
    font-size: 24px;
    color: #333;
    cursor: pointer;
    margin-left: 8px;
  }
  
  
  @media (max-width: 768px) {
  
    .health-card-container{
      margin-top: -350px;
      margin-left: 0px;
    }
  
  
    .health-card {
        min-width: 282px ;
        height: 518px;
    }
  
    .arrow-icon {
        display: none;
    }
  
    .health-card-titles{
      display: none;
    }
  }
  
  
  
  